
<template>
  <div class="error">
    <div class="missWarp">
      <div class="img404">
        <img src="../../assets/img404.png" alt="missing">
      </div>
      <p class="notfoundP">哎呀迷路了...</p>
      <div class="notfoundR">
        <p>可能的原因：</p>
        <ul>
          <li>原来的页面不存在了</li>
          <li>我们的服务器被外星人劫持了</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
}
</script>
<style lang="scss" scoped>
  .error{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;left: 0;
    background: #ffffff;
    $imgH: 155px;
    .missWarp{
      position: absolute;
      top: 30%;left: 50%;
      transform: translate(-50%, -50%);
      .img404{
        height: $imgH;
        position: relative;
        margin: 10px 0;
        img{
          display: block;
          width: $imgH;
          height: $imgH;
          position: absolute;
          top: 50%;left: 50%;
          transform: translate(-50%, -50%);
        }
      }
      .notfoundP{
        line-height: 22px;
        font-size: 17px;
        padding-bottom: 15px;
        border-bottom: 1px solid #f6f6f6;
        text-align: center;
        color: #262b31;
      }
      .notfoundR{
        color: #9ca4ac;
        font-size: 13px;
        line-height: 13px;
        text-align: left;
        width: 280px;
        margin: 0 auto;
        p{
          margin-top: 13px;
        }
        ul li{
          margin-top: 10px;
          margin-left: 36px;
        }
      }
    }
  }
</style>



